import React, { forwardRef, Fragment, useEffect, useRef, useState } from 'react';
import { bool, func, node, number, object, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import InputContainer from '../input-container';
import InputLabel from '../input-label';
import InputBar from '../input-bar';
import InputHint from '../input-hint';
import InputInput from '../input-input';
import InputText from '../input-text';
import InputMessage from '../input-message';
import InputOption from '../input-option';
import Icon from '../icon';

const CSS_STATE = 'vds-state';

const Input = forwardRef(({
  ariaLabel,
  checkmarkIcon,
  children,
  className,
  containerProps,
  contentAfterInput,
  contentBeforeInput,
  css,
  disabled,
  errorIcon,
  errorText,
  expanded,
  helpIcon,
  helpText,
  id = shortid.generate(),
  inputContainerProps,
  invalid,
  label,
  labelId,
  noValidate,
  onBlur,
  onChange,
  onFocus,
  placeholder,
  readOnly,
  showCheckmark,
  showErrorText,
  showHelpText,
  useNativePlaceholder,
  ...remainingProps
}, ref) => {
  const [dirty, setDirty] = useState(false);
  const [filled, setFilled] = useState(Boolean(remainingProps.value) || Boolean(remainingProps.defaultValue));
  const [focused, setFocused] = useState(false);
  const [isInvalid, setIsInvalid] = useState(invalid);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  const setValidity = e => {
    if (!noValidate) {
      if (dirty) {
        setIsInvalid((!e.target.checkValidity() || (e.target.required && !e.target.value.trim().length)));
      }

      if (!dirty && (e.target.required && !e.target.value.trim().length)) {
        setIsInvalid(true);
      }

      if (dirty && !e.target.required && !e.target.value.trim().length) {
        setIsInvalid(undefined);
      }
    }
  };

  useEffect(() => {
    setFilled(Boolean(remainingProps.defaultValue) || Boolean(remainingProps.value));
  }, [remainingProps.defaultValue, remainingProps.value]);

  useEffect(() => {
    setIsInvalid(invalid);
  }, [invalid]);

  const handleBlur = e => {
    setFocused(false);
    setValidity(e);

    if (typeof onBlur === 'function') {
      onBlur(e);
    }
  };

  const handleChange = e => {
    setFilled(e.target.value !== '');

    if (typeof isInvalid === 'boolean') {
      setValidity(e);
    }

    setDirty(true);
    if (typeof onChange === 'function') {
      onChange(e);
    }
  };

  const handleFocus = e => {
    setFocused(true);

    if (typeof onFocus === 'function') {
      onFocus(e);
    }
  };

  return (
    <InputContainer
      className={classnames(className)}
      css={css}
      disabled={disabled}
      expanded={expanded}
      filled={filled}
      focus={focused}
      readOnly={readOnly}
      invalid={isInvalid}
      {...containerProps}
    >
      {label ? <InputLabel id={labelId} htmlFor={uid}>{label}</InputLabel> : undefined}
      <InputBar>
        <InputInput>
          {contentBeforeInput}
          { placeholder && !useNativePlaceholder ? <InputHint>{placeholder}</InputHint> : undefined}
          <InputText
            aria-describedby={`input-message-help-${uid} input-message-error-${uid}`}
            aria-label={label ? undefined : ariaLabel}
            aria-invalid={isInvalid}
            id={uid}
            disabled={disabled}
            onBlur={handleBlur}
            onChange={handleChange}
            onFocus={handleFocus}
            placeholder={useNativePlaceholder ? placeholder : undefined}
            readOnly={readOnly}
            ref={ref}
            {...inputContainerProps}
            {...remainingProps}
          />
        </InputInput>
        {
          showCheckmark && typeof isInvalid === 'boolean' && !isInvalid && Boolean(checkmarkIcon)
            ? <InputOption
              className={classnames({
                [`${CSS_STATE}--hidden`]: typeof isInvalid === 'boolean' && isInvalid
              })}
            >
              {checkmarkIcon}
            </InputOption>
            : undefined
        }
        {contentAfterInput}
        {children}
      </InputBar>
      <InputMessage
        id={`input-message-help-${uid}`}
        invalid={isInvalid}
        show
      >
        {
          !isInvalid && focused && showHelpText && Boolean(helpText)
            ? <Fragment>{helpIcon} {helpText}</Fragment>
            : ''
        }
      </InputMessage>
      <InputMessage
        id={`input-message-error-${uid}`}
        invalid={isInvalid}
        show
      >
        {
          isInvalid === true && showErrorText && Boolean(errorText)
            ? <Fragment>{errorIcon} {errorText}</Fragment>
            : ''
        }
      </InputMessage>
    </InputContainer>
  );
});

Input.propTypes = {
  /**
   * Aria Label
   */
  ariaLabel: string,

  /**
   *  Checkmark Icon
   */
  checkmarkIcon: node,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Container Props
   */
  containerProps: object, // eslint-disable-line

  /**
  * Content After input use for icons, etc
  */
  contentAfterInput: node,

  /**
  * Content Before input use for icons, etc
  */
  contentBeforeInput: node,

  /**
   * @ignore
   */
  css: func,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   *  Error Message Icon
   */
  errorIcon: node,

  /**
   *  Error Message
   */
  errorText: node,

  /**
   *  Expanded
   */
  expanded: bool,

  /**
   * Input Help Message Icon
   */
  helpIcon: node,

  /**
   * Input Help Message
   */
  helpText: node,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Input Container Props, applies to .vds-input-text
   */
  inputContainerProps: object, // eslint-disable-line

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Label
   */
  label: node,

  /**
   * Label Id
   */
  labelId: oneOfType([number, string]),

  /**
   * Disable native HTML5 validation e.g. type=email, pattern="some regex pattern"
   */
  noValidate: bool,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * On Change
   */
  onChange: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /**
   * Placeholder
   */
  placeholder: node,

  /**
   * ReadOnly
   */
  readOnly: bool,

  /**
   * Show Checkmark Icon
   */
  showCheckmark: bool,

  /**
   * Show Error Message
   */
  showErrorText: bool,

  /**
   * Show Help Message
   */
  showHelpText: bool,

  /**
   * Use native placeholder
   */
  useNativePlaceholder: bool
};

Input.defaultProps = {
  checkmarkIcon: <Icon name="checkmark" resolution="low" />,
  errorIcon: <Icon name="error" resolution="tiny" />
};

Input.displayName = 'Input';

export default Input;
