import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card-indicator';

const InputCardIndicator = ({
  children,
  className,
  type,
  ...remainingProps
}) => (
  <div
    aria-label={Boolean(type) ? type : 'card'}
    aria-live="polite"
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${type}`]: Boolean(type)
    }, className)}
    role="img"
    {...remainingProps}
  >
    {children}
  </div>
);

InputCardIndicator.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Type
  */
  type: oneOf([
    'amex',
    'discover',
    'elo',
    'mastercard',
    'visa'
  ])
};

InputCardIndicator.displayName = 'InputCardIndicator';

export default InputCardIndicator;
