import React, { useState } from 'react';
import { utilities } from '@visa/ui-utils/lib/js/card-number';
import Col from '../../col';
import Grid from '../../grid';
import InputCard from '../../input-card';
import Row from '../../row';
import Typography from '../../typography';

const { validator } = utilities;

const InputCardExample = () => {
  const [cardBrand, setCardBrand] = useState(null);
  const [cardNumber, setCardNumber] = useState('');
  const [error, setError] = useState('');
  const [isSupportedBrand, setIsSupportedBrand] = useState(true);
  const [isValidCardNumber, setIsValidCardNumber] = useState(true);
  const acceptedCards = ['AMEX', 'DISCOVER', 'ELO', 'MASTERCARD', 'VISA'];
  const cardBins = [
    400444,
    412356,
    506717,
    512346,
    650590,
    612345
  ];
  const checkMod11 = false;
  const checkMods = true;

  const getInlineError = (type, brand) => {
    const inlineErrors = {
      'EMPTY': 'You need to enter a value',
      'INVALID_BIN': 'That is not a real card brand',
      'INVALID_BIN_BRAND': `You sure that is a real ${brand} card?`,
      'INVALID_GENERIC': 'Something is not correct',
      'NOT_SUPPORTED': 'Apologies but we do not accept that card brand',
      'PARTIAL': 'Incomplete card number!'
    };

    return inlineErrors[type];
  };

  const handleBlur = () => {
    const isBlur = true;
    const conditions = {
      brand: cardBrand,
      checkMod11,
      checkMods,
      isSupported: isSupportedBrand,
      isValid: isValidCardNumber,
      value: cardNumber
    };

    const errorType = validator(isBlur, conditions);
    const errorMessage = getInlineError(errorType, cardBrand);

    setIsValidCardNumber(Boolean(errorMessage) !== true);
    setError(errorMessage);
  };

  const handleChange = (e, { brand, isSupported, isValid, value }) => {
    const isBlur = false;
    const conditions = {
      brand: cardBrand,
      checkMod11,
      checkMods,
      isSupported,
      isValid,
      value
    };

    const errorType = validator(isBlur, conditions);
    const errorMessage = getInlineError(errorType, brand);

    setCardBrand(brand);
    setCardNumber(value);
    setIsSupportedBrand(isSupported);
    setIsValidCardNumber(isValid);
    setError(errorMessage);
  };

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Input Card</Typography>
          <Typography>All Card Brands Accepted with Full Validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <form noValidate>
            <InputCard
              acceptedCards={acceptedCards}
              cardBins={cardBins}
              checkMods
              errorText={error}
              handleCardNumberChange={handleChange}
              helpText="Enter your card number here"
              invalid={!isValidCardNumber}
              label="Card Number"
              onBlur={handleBlur}
              placeholder="15 to 16 digits"
              required
              shouldValidateAcceptedCards
              showErrorText
              showHelpText
              validateMod11
            />
          </form>
        </Col>
      </Row>

    </Grid>
  );
};

export default InputCardExample;
