import React, { forwardRef, useRef, useState } from 'react';
import { arrayOf, bool, func, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import { constants, utilities } from '@visa/ui-utils/lib/js/card-number';
import { useTextMask } from '@visa/ui-utils/lib/js/text-mask/react';
import Input from '../input';
import InputOption from '../input-option';
import InputCardIndicator from '../input-card-indicator';

const CSS_PREFIX = 'vds-input-card';

const {
  AMEX,
  AMEX_MASK,
  LENGTH_AMEX,
  LENGTH_DEFAULT,
  NON_AMEX_MASK
} = constants;

const {
  acceptedBrandsCheck,
  binCheck,
  brands,
  checkMod,
  filterAcceptedBrands,
  possibleBrand
} = utilities;

/**
 * @return {JSX}
 */
const InputCard = forwardRef(({
  acceptedCards = brands,
  cardBins,
  checkMods,
  className,
  css,
  errorText,
  handleCardNumberChange,
  id = shortid.generate(),
  inputMode,
  isValidCard,
  label,
  name,
  placeholder,
  shouldValidateAcceptedCards,
  type,
  validateMod11,
  ...remainingProps
}, ref) => { // eslint-disable-line no-unused-vars
  const [value, setValue] = useState(
    remainingProps.value
      ? remainingProps.value
      : remainingProps.defaultValue
        ? remainingProps.defaultValue
        : ''
  );
  const [cardBrand, setCardBrand] = useState(value ? possibleBrand(value, cardBins) : '');
  const isValidBin = value ? binCheck(value, cardBins) : isValidCard;

  const isSupportedCard =
    shouldValidateAcceptedCards && acceptedCards ? acceptedBrandsCheck(cardBrand, acceptedCards) : true;
  const partnerAcceptedCards =
    shouldValidateAcceptedCards && acceptedCards ? filterAcceptedBrands(acceptedCards) : acceptedCards;

  // eslint-disable-next-line no-unused-vars
  const [isValidCardNum, setIsValidCardNum] = useState(
    shouldValidateAcceptedCards ? isValidBin && isSupportedCard : isValidBin
  );
  const [maxLength, setMaxLength] = useState(cardBrand === AMEX ? LENGTH_AMEX : LENGTH_DEFAULT);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;
  const __isPartnerAccepted = isSupportedCard;

  /**
   * @description check entered value for validity then pass that info to caller for its validation and own purposes
   * Since caller owns and determines validation it will need to inform CardNumber if entered value is valid
   * along with the desired error message.
   *
   * @param {SyntheticEvent} e
   */
  const handleValueChange = e => {
    // e.persist();
    const cardInput = e.target;
    const enteredValue = cardInput.value;
    const enteredCardNumber = enteredValue.replace(/\s/g, '');
    const possibleEnteredBrand = possibleBrand(enteredCardNumber, cardBins);
    const isEnteredBinValid = binCheck(enteredCardNumber, cardBins);
    const shouldCheckAcceptedBrands = shouldValidateAcceptedCards && partnerAcceptedCards;
    const isEnteredCardSupported = shouldCheckAcceptedBrands
      ? acceptedBrandsCheck(possibleEnteredBrand, partnerAcceptedCards)
      : __isPartnerAccepted;

    setCardBrand(possibleEnteredBrand);
    setIsValidCardNum(shouldCheckAcceptedBrands ? isEnteredBinValid && isEnteredCardSupported : isEnteredBinValid);
    setValue(enteredValue);

    const shouldCheckMod = enteredValue.length === maxLength && checkMods;
    const passesMod = shouldCheckMod ? checkMod(enteredCardNumber, validateMod11) : true;

    setMaxLength(possibleEnteredBrand === AMEX ? LENGTH_AMEX : LENGTH_DEFAULT);

    handleCardNumberChange &&
      handleCardNumberChange(e, {
        brand: possibleEnteredBrand,
        isSupported: isEnteredCardSupported,
        isValid: shouldCheckAcceptedBrands ? isEnteredBinValid && isEnteredCardSupported : isEnteredBinValid,
        passesMod,
        value: e.target.value
      });
  };

  /**
   * @description handle change event but only for numbers-only values so field error state is correct
   * @param {SyntheticEvent} e
   */
  const handleOnChange = e => {
    if (!isNaN(e.target.value.replace(/\s/g, ''))) {
      handleValueChange(e);
    }
  };

  remainingProps.mask = cardBrand === AMEX ? AMEX_MASK : NON_AMEX_MASK;
  remainingProps.onChange = handleOnChange;

  const textMaskProps = useTextMask(remainingProps).getInputProps;

  return (
    <div
      className={classnames(CSS_PREFIX, className)}
      css={css}
    >
      <Input
        {...remainingProps}
        {...textMaskProps}
        errorText={errorText}
        id={uid}
        inputMode={inputMode}
        label={label}
        maxLength={maxLength}
        name={name}
        onBlur={remainingProps.onBlur}
        onFocus={remainingProps.onFocus}
        onKeyUp={remainingProps.onKeyUp}
        placeholder={placeholder}
        // ref={ref}
        type={type}
        value={value}
      >
        <InputOption>
          <InputCardIndicator
            type={brands.includes(cardBrand) ? cardBrand.toLowerCase() : undefined}
          />
        </InputOption>
      </Input>
    </div>
  );
});

InputCard.propTypes = {
  /**
   * @type {!array}
   * Accepted cards
   */
  acceptedCards: arrayOf(string),

  /**
   * @type {!string}
   * Auto complete
   */
  autoComplete: string,

  /**
   * @type {!array}
   * Card Bins
   */
  cardBins: arrayOf(number).isRequired,

  /**
   * @type {!bool}
   * Check Mods
   */
  checkMods: bool,

  /**
   * @type {!string}
   * @ignore
   */
  className: string,

  /**
   * @type {!string}
   * @ignore
   */
  css: func,

  /**
   * @type {!string}
   * Error
   */
  errorText: string,

  /**
   * @type {?function(Event)}
   * Handle card number change
   */
  handleCardNumberChange: func,

  /**
   * While overriding this value is likely to not cause issues it is recommended to override with caution.
   * See jsdoc for `name` for more information.
   */
  id: oneOfType([number, string]),

  /**
   * Input Mode to force mobile screens to use large number pad
   */
  inputMode: string,

  /**
   * @type {bool}
   * is valid card
   */
  isValidCard: bool,

  /**
   * Label
   */
  label: string,

  /**
   * @type {!string}
   *
   * Note: It is not recommended to override this name. It must follow a specific convention
   to support card scanning and autocomplete.
   * See: https://developers.google.com/web/fundamentals/design-and-ux/input/forms/#recommended_input_name_and_autocomplete_attribute_values
  for more information.
   * This name has been chosen based on experiences with real-world usage of applications.
   *
   * Variations of this value have not been tested and may not be supported, so override with caution.
   *
   * Overriding this value should be done only if there are multiple cardNumber fields present in the same form
   or on the same screen at the same time.
   */
  name: string,

  /**
   * @type {?function(Event)}
   * On Blur
   */
  onBlur: func,

  /**
   * @type {?function(Event)}
   * On Focus
   */
  onFocus: func,

  /**
   * @type {?function(Event)}
   * On Keyup
   */
  onKeyUp: func,

  /**
   * Placeholder
   */
  placeholder: string,

  /**
   * @type {!bool}
   * Should validate accepted cards
   */
  shouldValidateAcceptedCards: bool,

  /**
   * Type
   */
  type: string,

  /**
   * @type {bool}
   * Validate mod 11
   */
  validateMod11: bool
};

InputCard.defaultProps = {
  autoComplete: 'cc-number',
  inputMode: 'decimal',
  isValidCard: true,
  name: 'cardNumber',
  type: 'text'
};

InputCard.displayName = 'InputCard';

export default InputCard;
